@media (hover: hover) {
     ::-webkit-scrollbar {
        width: 6px;
        height: 6px;
        background: map-get($colors, 'white');
    }
     ::-webkit-scrollbar-thumb {
        background: map-get($colors, 'blue');
       border-radius: $radius-xs;
    }
}

::selection {
    background-color: map-get($colors, 'blue');
    color: map-get($colors, 'light');
}

body.hidden{
  overflow: hidden;
