  display: inline-block;
  text-decoration: none;
  padding: 10px;
}

.b-footer__link * {
  transition: all 0.25s ease;
}

.b-footer__link:hover * {
  fill: #ffffff;
}

.b-header {
  background-color: #F7F9FB;
  position: relative;
  z-index: 151;
}

.b-header_fixed {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  animation: headerSticky 0.15s linear forwards;
}

@keyframes headerSticky {
  0% {
    transform: translate3d(0, -100%, 0);
  }

  100% {
    transform: translate3d(0, 0, 0);
  }
}

.b-header__container {
  height: 78px;
}

.b-header__inner {
  padding-top: 20px;
  padding-bottom: 20px;
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

.b-header__logo {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  cursor: pointer;
  transition: all 0.25s ease;
  margin-right: 40px;
}

.b-header__logo:hover {
  opacity: 0.75;
}

.b-header__logo img {
  display: block;
  width: 100%;
}

.b-header__menu {
  margin-left: auto;
  margin-right: 40px;
}

.b-header__buttons {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: 40px;
}

.b-header__button:not(:last-child) {
  margin-right: 15px;
}

.b-header__lang {
  margin-left: auto;
}

.b-header__mobile-menu-trigger {
  display: none;
}

.b-header__mobile-menu-trigger svg:nth-child(2) {
  display: none;
}

.b-header__mobile-menu-trigger_active svg:nth-child(1) {
  display: none;
}

.b-header__mobile-menu-trigger_active svg:nth-child(2) {
  display: block;
  transform: scale(1.2);
}

@keyframes mobileMenuTriggerSvg {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

