.b-group-slider__arrow {
  width: 40px;
  height: 40px;
  cursor: pointer;
  position: absolute;
  top: calc(100% - 20px - 14px);
  transform: scale(0.7);
}

.b-group-slider__arrow:before {
  content: '';
  position: absolute;
  top: calc(50% - 2px);
  height: 4px;
  width: 22px;
  left: calc(50% - 14px);
  background-color: #00c67a;
}

.b-group-slider__arrow.slick-disabled:before {
  background-color: #e6e6e6;
}

.b-group-slider__arrow_prev {
  right: calc(50% + 100px);
}

.b-group-slider__arrow_prev:after {
  content: '';
  position: absolute;
  top: calc(50%);
  height: 15px;
  width: 15px;
  left: 2px;
  background-color: transparent;
  transform-origin: top left;
  transform: rotate(-45deg);
  border-top: 4px solid #00c67a;
  border-left: 4px solid #00c67a;
}

.b-group-slider__arrow_prev.slick-disabled:after {
  border-top: 4px solid #e6e6e6;
  border-left: 4px solid #e6e6e6;
}

.b-group-slider__arrow_next {
  left: calc(50% + 100px);
}

.b-group-slider__arrow_next:after {
  content: '';
  position: absolute;
  top: calc(50%);
  height: 15px;
  width: 15px;
  right: 2px;
  background-color: transparent;
  transform-origin: top right;
  transform: rotate(45deg);
  border-top: 4px solid #00c67a;
  border-right: 4px solid #00c67a;
}

.b-group-slider__arrow_next:before {
  left: calc(50% - 8px);
}

.b-group-slider__arrow_next.slick-disabled:after {
  border-top: 4px solid #e6e6e6;
  border-right: 4px solid #e6e6e6;
